import { Component } from "../src/component/Component";
import { declareComponent } from "../src/component/declareComponent";
import { Loadable } from "../src/component/Loadable";
import { useEffect, useState, useCallback } from "../src/hooks";

type TypeReduxAppProps = {
    updateTestApp: Function;
    testApp: string;
};

type TypeReduxAppState = {
    testApp?: string;
};

const AsyncApp = Loadable({
    loader: () => import(/* webpackChunkName: "AsyncApp" */ "./AsyncApp")
})

const AsyncRouter = Loadable({
    loader: () => import(/* webpackChunkName: "AsyncRouter" */ "./AsyncRouter"),
    loading: () => '<h2>Hellow world</h2>'
});

@declareComponent({
    selector: "ReduxApp",
    components: {
        AsyncApp,
        AsyncRouter
    },
    connect: {
        mapStateToProps: (state) => {
            return {
                ...state.app
            };
        },
        // tslint:disable-next-line: object-literal-sort-keys
        mapDispatchToProps: (dispatch) => ({
            updateTestApp: (data) => (dispatch(data))
        }),
        reducers: [
            {
                name: "app",
                // tslint:disable-next-line: object-literal-sort-keys
                callback: (state, action) => {
                    switch(action.type) {
                        case "TestApp": {
                            return {
                                ...state,
                                testApp: action.data
                            };
                        }
                        default: {
                            return {
                                testApp: "Init App"
                            };
                        }
                    }
                }
            }
        ]
    }
})
export class ReduxApp extends Component<TypeReduxAppProps, TypeReduxAppState> {
    constructor(props: TypeReduxAppProps) {
        super(props);
        this.state = {
            testApp: props.testApp
        };
    }
    $willReceiveProps(props: TypeReduxAppProps): void {
        console.log("update---", props.testApp);
        this.setState({
            testApp: props.testApp
        });
    }
    onClick(): void {
        this.props.updateTestApp({
            type: "TestApp",
            // tslint:disable-next-line: object-literal-sort-keys
            data: "New State - " + (new Date()).getTime()
        });
    }
    render(): any {
        return `<div>
            <span>__Test data is from redux__: {{props.testApp}}</span>
            <button et:click="onClick">Update Redux</button>
            <AsyncApp />
        </div>`;
    }
}

export default (props) => {
    const [{}, setScore, getScore] = useState("score", 1122);
    const [{}, setVisible] = useState("visible", false);
    const [{}, setTitle] = useState('title', props.title);
    useCallback(() => {
        setScore(getScore() + 1);
        setVisible(true);
    }, {
        name: "onClick"
    });
    useEffect<any>((name, {props}) => {
        setTitle(props.title);
        return () => {
            console.log('useEffect unMount', 'haha');
        };
    });
    return `<div>
        <label>
            <button et:click='onClick'>
                <span>Hook TestButton{{state.score}}__{{state.title}}</span>
                <i show="{{state.visible}}">Visible_Test{{props.title}}</i>
            </button>
        </label>
        <hr/>
        <eui-redux-app />
    <div>`;
}
