import { Component } from "../src/component/Component";
import { declareComponent } from "../src/component/declareComponent";
import { ElmerDOM } from "../src/domEvent/ElmerDom";
import { IElmerResizeEvent } from "../src/events/IElmerEvent";
import { useEffect, useState } from "../src/hooks";
import { autowired } from "../src/inject/injectable";
import { PropTypes } from "../src/propsValidation";
import Button from "./Button";

type TypeDemoAppState = {
    time: string;
    index: number;
    app: any;
    listData?: any[];
    demoApp?: string;
};
type TypeDemoAppProps = {
    title: string;
    actionRegion: Function;
}

const TestApp = (props:any, options) => {
    const setState1 = useState("test1", '001100');
    const setState2 = useState("test2", false);
    props.time = props.time + "__APP__";
    return "<div><select><option>Hello world_{{state.test1}}</option></select><a href=''>Hello XXS{{state.test2}}</a></div>";
}


@declareComponent({
    selector: "demo-app",
    components: {
        TestApp: TestApp,
        Button: Button
    },
    connect:{
        mapStateToProps: (state:any, obj:any) => {
            return {demoApp: obj.getValue(state,"demo.demoApp") || "init App"};
        },
        mapDispatchToProps: (dispatch) => ({
            actionRegion: (data) => (dispatch(data))
        }),
        reducers: [
            {
                name: "demo",
                callback: (state, action) => {
                    switch(action.type) {
                        case "DEMO":
                            return {
                                version: "test"
                            };
                        case "ChangeRegion": {
                            console.log("update from redux");
                            return {
                                ...state,
                                demoApp: action.data
                            }
                        }
                            default:
                                return {
                                    demoApp: true,
                                    demoTitle: "redux demo"
                                };
                    }
                }
            }
        ]
    }
})
export default class DemoApp extends Component<TypeDemoAppProps,TypeDemoAppState> {
    static propType = {
        title: {
            rule: PropTypes.string
        },
        actionRegion: {
            rule: PropTypes.object.isRequired
        }
    }
    state = {
        time: "",
        index: 0,
        app: "App",
        listData: [],
        showList: true
    }
    @autowired(ElmerDOM)
    private $: ElmerDOM;
    $init(): void {
        for(let i=0;i<5;i++) {
            this.state.listData.push({
                title: "App_" + i,
                value: i
            });
        }
    }
    onClick(): void {
        this.props.actionRegion({
            type: "ChangeRegion",
            data: (new Date()).getTime().toString() + "_by_redux",
            index: this.state.index + 1
        });
        console.log("onEventChange");
    }
    $resize(evt:IElmerResizeEvent): void {
        console.log(evt.width, evt.height, evt.outWidth, evt.outHeight);
    }
    $willReceiveProps(props): void {
        if(props.demoApp !== this.state.time) {
            this.setState({
                time: props.demoApp
            })
        }
    }
    onCloseAnimation() {
        const ulDom = this.dom['test'];
        if(this.state.showList) {
            this.$.slideOut(ulDom);
            this.setState({
                showList: false,
            });
        } else {
            this.$.slideIn(ulDom);
            this.setState({
                showList: true
            });
        }
        
    }
    $render() {
        return `<div><button et:click="onClick">OnClickEvent</button><span>my class<b>format {{state.time}}</b> staff, stateIndex: {{state.index}}</span>
        <div><context/></div>
        <button et:click='onCloseAnimation'>{{state.showList ? '' : ''}}</button>
        <ul  id='test' style="overflow:hidden;">
        <li em:for="let item in this.state.listData"><span>{{item.title}}</span></li>
        </ul>
        </div>
        <TestApp time="{{state.app}}"/><Button title="{{state.showList ? '' : ''}}" id="diffDebugger" />`;
    }
}