import { IElmerEvent } from "../src/events/IElmerEvent";
import { ElmerUI } from "../src/render/ElmerUI";
import "./DemoApp";
import "../src/widget/redux/ReduxProvider";

const ui = new ElmerUI();

ui.onReady(() => {
    const exampleApp = {
        title: "for new framework testting",
        version: "20201205",
        state: {
            count: 0,
            testData: [],
            visible: false
        },
        onClick: function(evt:IElmerEvent) {
            evt.cancelBubble = true;
            this.setState({
                count: this.state.count + 1
            });
        },
        onPress: function(evt) {
            console.log("onMouseDown", evt);
        },
        onVisibleClick: function() {
            const myVisible = !this.state.visible;
            this.setState({
                visible: myVisible
            });
        },
        onInput: function(evt) {
            console.log(evt);
        },
        onButtonClick: function() {
            const oldData = JSON.parse(JSON.stringify(exampleApp.state.testData));
            oldData.push({
                title: "demo" + oldData.length,
                value: oldData.length
            });
            this.setState({
                testData: oldData
            });
        },
        // tslint:disable-next-line: object-literal-sort-keys
        render:() => {
            return "<eui-redux-provider>\n    <div>\n        <li>\n            <div et:click=\"onClick\">\n                <span data-value='app' data-label='for test'>ClickEventA{{version}}</span>\n            </div>\n        </li>\n        <li><span>B{{title}}</span></li>\n        <label id=\"testDom\" data-value=\"haha\" if=\"{{state.visible}}\"></label>\n        <li><span>E{{version}}</span></li>\n        <button et:click=\"onButtonClick\" id=\"keyTest\">Update State{{state.count}}</button>\n        <forEach data=\"state.testData\" item=\"demo\" index=\"mapKey\">\n            <input key=\"App_{{mapKey}}\" type=\"text\" value=\"{{demo.title}}\"/>\n        </forEach>\n        <button et:click=\"onVisibleClick\" style=\"display:block;\">{{state.visible ? '' : ''}}</button>\n        <textarea  et:input=\"onInput\" style=\"display:block;\"></textarea>\n        <div>\n            <eui-demo-app time=\"{{state.count}}\">\n                <div><button>Dingo</button></div>\n            </eui-demo-app>\n        </div>\n        <header>Hello world</header>\n        <div data-type=\"html\">\n            <svg xmlns=\"http://www.w3.org/2000/svg\" xmlns:xlink=\"http://www.w3.org/1999/xlink\" width=\"48px\" height=\"60px\" viewBox=\"0 0 24 30\" style=\"enable-background:new 0 0 50 50\" xml:space=\"preserve\">\n                <rect x=\"0\" y=\"9.22656\" width=\"4\" height=\"12.5469\" fill=\"#FF6700\">\n                    <animate attributeName=\"height\" attributeType=\"XML\" values=\"5;21;5\" begin=\"0s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                    <animate attributeName=\"y\" attributeType=\"XML\" values=\"13; 5; 13\" begin=\"0s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                </rect>\n                <rect x=\"10\" y=\"5.22656\" width=\"4\" height=\"20.5469\" fill=\"#FF6700\">\n                    <animate attributeName=\"height\" attributeType=\"XML\" values=\"5;21;5\" begin=\"0.15s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                    <animate attributeName=\"y\" attributeType=\"XML\" values=\"13; 5; 13\" begin=\"0.15s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                </rect>\n                <rect x=\"20\" y=\"8.77344\" width=\"4\" height=\"13.4531\" fill=\"#FF6700\">\n                    <animate attributeName=\"height\" attributeType=\"XML\" values=\"5;21;5\" begin=\"0.3s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                    <animate attributeName=\"y\" attributeType=\"XML\" values=\"13; 5; 13\" begin=\"0.3s\" dur=\"0.6s\" repeatCount=\"indefinite\"></animate>\n                </rect>\n            </svg>\n        </div>\n    </div>\n</eui-redux-provider>";
        }
    };
    console.time("DemoApp");
    console.timeLog("DemoApp", "start render");
    ui.render(document.getElementById("app"), exampleApp);
    console.timeEnd("DemoApp");
});
